@charset "UTF-8";
@import 'https://fonts.googleapis.com/css?family=Roboto:400,400i,700,700i&amp;subset=cyrillic';
@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

* {
  padding: 0;
  margin: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/* 
 * BrowserSync Notification */
#__bs_notify__ {
  display: none !important; }

@keyframes navtoggleshake {
  from {
    opacity: 0;
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    transform: translate(0, 0); }
  50% {
    transform: translate(0, -0.5rem); }
  100% {
    transform: translate(0, 0); } }

@keyframes navitemin {
  from {
    opacity: 0;
    transform: translateX(3rem); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes shake {
  0% {
    transform: translate(-2rem, 0); }
  25% {
    transform: translate(2rem, 0); }
  50% {
    transform: translate(-2rem, 0); }
  75% {
    transform: translate(2rem, 0); }
  100% {
    transform: translate(0, 0); } }

html {
  font: var(--font-size-initial)/1.5 "Roboto", Helvetica, sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

.site-title,
h1, h2, h3, h4, h5, h6 {
  margin: 1em 0 0.5em 0;
  padding: 0; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

h5, h6 {
  font-weight: bold; }

p {
  margin: 0;
  padding-bottom: 1.5rem; }

ol, ul, dl {
  margin: 0 0 1.5rem 0; }

ul, ol {
  padding: 0;
  margin-left: 1.5rem; }

ul li, ol li {
  list-style-position: inside; }

ul ul {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

ul li {
  list-style-type: square; }

ul ul li {
  list-style-type: disc; }

ul ul ul li {
  list-style-type: circle; }

blockquote {
  width: 40%;
  min-width: 15rem;
  border-left: 4px solid #DDD;
  font-style: italic;
  padding: 1rem 2rem;
  position: relative;
  color: #444;
  font-family: "Georgia", serif;
  padding-top: 0; }

blockquote .author {
  text-align: right; }

table {
  width: 100%;
  table-layout: fixed;
  margin-bottom: 1.5rem;
  border: 1px solid #CCC;
  border-spacing: 0;
  border-collapse: collapse; }
  table td, table th {
    border: 1px solid #CCC;
    padding: 0.3rem 0.5rem; }
  table thead th {
    text-align: left;
    background: #CCC; }
  table tr:nth-child(odd) td {
    background: #F1F1F1; }
  table tr:hover td {
    background: black;
    color: #FFF;
    border-color: transparent; }

hr {
  margin: 2rem 0; }

img[class^='align'] {
  padding: 0.5rem;
  background: #FFF;
  border: 3px solid #222;
  box-shadow: 0 0 5rem 0.1rem rgba(0, 0, 0, 0.25); }

.align-left {
  float: left;
  margin: 0 1.5rem 1rem 0; }

.align-right {
  float: right;
  margin: 0 0 1rem 1.5rem; }

#nav-main-toggle {
  display: none; }

.navigation {
  display: none; }
  .navigation ul {
    padding: 1rem 1.5rem 2rem 1.5rem;
    margin: 0;
    background: #000;
    width: 100vw;
    color: #FFF; }
    .navigation ul li {
      list-style-type: none;
      padding: 1rem 0;
      border-bottom: 0.1px solid #888;
      animation: navitemin 0.3s 1 ease forwards; }
      .navigation ul li a {
        text-decoration: none;
        color: #FFF;
        font-size: 1rem;
        text-transform: uppercase; }
        .navigation ul li a:before {
          color: #FFF;
          font-family: 'Font Awesome 5 Free';
          font-weight: bold;
          display: inline-block;
          margin-right: 1rem; }
        .navigation ul li a.active, .navigation ul li a.active:before {
          color: #DF9D00; }
        .navigation ul li a:hover:before, .navigation ul li a:hover {
          color: #DF9D00; }
    .navigation ul li:last-child {
      border-bottom: none;
      padding-bottom: 1rem; }

#nav-main-toggle:checked + .site-header .navigation {
  display: flex; }

#nav-main-toggle:checked + .site-header .nav-toggle:before {
  content: '\f00d';
  animation: jump 0.3s 1 ease-in-out forwards; }

.navigation ul li:nth-child(1) a:before {
  content: ""; }

.navigation ul li:nth-child(2) a:before {
  content: ""; }

.navigation ul li:nth-child(3) a:before {
  content: ""; }

.navigation ul li:nth-child(4) a:before {
  content: ""; }

.navigation ul li:nth-child(5) a:before {
  content: ""; }

.navigation ul li:nth-child(6) a:before {
  content: ""; }

.navigation ul li:nth-child(7) a:before {
  content: ""; }

.navigation ul li:nth-child(8) a:before {
  content: ""; }

ul.gallery {
  padding: 0;
  margin: 0;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(2, 3fr);
  grid-template-rows: auto 1fr auto;
  grid-auto-flow: dense; }
  @media screen and (min-width: 400px) {
    ul.gallery {
      grid-template-columns: repeat(3, 1fr); } }
  @media screen and (min-width: 600px) {
    ul.gallery {
      grid-template-columns: repeat(4, 1fr); } }
  @media screen and (min-width: 800px) {
    ul.gallery {
      grid-template-columns: repeat(5, 1fr); } }
  @media screen and (min-width: 1024px) {
    ul.gallery {
      grid-template-columns: repeat(6, 1fr); } }
  ul.gallery li {
    list-style-type: none;
    position: relative;
    overflow: hidden; }
    ul.gallery li:before {
      display: block;
      content: '';
      padding-top: 150%; }
    ul.gallery li.span-c2 {
      grid-column: span 2;
      grid-row: span 2; }
    ul.gallery li.span-r2 {
      grid-row: span 2; }
    ul.gallery li.span-c3 {
      grid-column: span 3; }
    ul.gallery li.span-c31 {
      grid-column: span 3; }
      ul.gallery li.span-c31:before {
        padding-top: 33.33%; }
  ul.gallery a {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  ul.gallery img {
    display: block;
    width: auto;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  ul.gallery li.img-p img {
    width: 150%;
    height: auto; }

header.site-header div.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  color: #FFF;
  background: #000; }

div.wrapper .site-title a {
  font-size: 1.5rem;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFF;
  margin-left: 1rem;
  font-weight: bold;
  animation: navtoggleshake 0.5s 1 ease-in-out forwards; }

#nav-toggle {
  font-size: 1.5rem;
  margin-right: 1rem;
  font-weight: bold;
  animation: navtoggleshake 0.5s 1 ease-in-out forwards; }

#nav-toggle:before {
  display: block;
  content: '\f0c9'; }

footer {
  margin-top: auto;
  background: #000;
  color: #FFF;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center; }
  footer div {
    animation: navitemin 0.3s 1 ease forwards; }
  footer .social a {
    margin-right: 0.7rem;
    font-size: 1.5rem;
    color: white; }
    footer .social a:hover {
      color: #DF9D00; }

fieldset,
fieldset legend {
  border: 2px solid #CCC;
  box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.1); }

fieldset {
  background: #F7F7F7;
  padding: 3.5vh 4.5vw 3vh 4.5vw; }

fieldset legend {
  display: inline-block;
  background: #F7F7F7;
  padding: 0.6em 2.2em;
  font-weight: bold; }

input[type="submit"] {
  display: block;
  border: 2px solid #DF9D00;
  background: #FFF;
  color: #000;
  padding: 0.8em 2em;
  font-weight: bold;
  cursor: pointer; }

input[type="submit"]:hover {
  background: #DF9D00;
  color: #FFF;
  transition: background 0.3s ease, border-color 0.3s ease; }

form {
  animation: shake 0.5s 1 ease-in-out forwards; }

.field {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }

.field > label {
  flex: 0 0 6rem;
  text-align: right;
  padding-right: 0.5rem; }

.input, textarea {
  flex: 1 0 auto;
  display: flex;
  position: relative;
  align-items: center; }

.input input, textarea {
  order: 1;
  border: none;
  padding: 0.5rem 0.6rem;
  position: relative;
  z-index: 3;
  background: none;
  outline: none;
  flex: 1 0 auto; }

.input .fas {
  order: 0;
  position: relative;
  z-index: 2;
  line-height: 1;
  padding: 0.2rem 0.5rem 0.2rem 0.7rem;
  font-size: 0.8rem; }

.input .actions {
  background: #FFF;
  border: 1px solid #CCC;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1; }

.input textarea:focus + .actions,
.input input:focus + .actions {
  border-color: #DF9D00; }

.input input:focus + .actions + .fas {
  color: #DF9D00; }

textarea {
  resize: none; }

body {
  display: flex;
  flex-direction: column; }

@media only screen and (min-width: 1024px) {
  body {
    height: 100vh;
    overflow: hidden;
    flex-direction: row;
    justify-content: space-between; }
    body footer:last-of-type {
      height: 100vh;
      flex-direction: column; }
      body footer:last-of-type .author {
        margin-top: 40rem; }
  header.site-header {
    width: 20rem; }
  #nav-toggle {
    display: none; }
  .navigation {
    display: block; }
    .navigation ul {
      width: 100%;
      height: 100vh; }
  div.wrapper .site-branding {
    background: #FFCC00;
    width: 70%;
    margin: 4rem auto;
    position: relative;
    padding: 2rem 1rem 2rem 1rem;
    border-radius: 0.5rem; }
  div.wrapper .site-title {
    display: flex;
    justify-content: center;
    align-items: center; }
    div.wrapper .site-title a {
      color: #000; }
  div.wrapper .site-branding::after {
    position: absolute;
    bottom: -40px;
    right: 0;
    content: 'f';
    font-weight: bold;
    font-size: 5em;
    color: #000; }
  footer.site-footer {
    width: 11rem; }
    footer.site-footer .social {
      display: flex;
      justify-content: space-around;
      padding: 1rem; }
    footer.site-footer .author {
      font-size: 0.75rem; } }

main {
  background: #F7F7F7;
  width: 100%;
  padding: 2rem; }
  @media only screen and (max-width: 1023px) {
    main {
      height: 90vh; } }
  main section {
    width: 85%;
    margin: 0 auto;
    font-size: 1.3rem; }
    main section h2 {
      font-weight: normal; }
      main section h2:after {
        content: '';
        display: block;
        border: 0.1rem solid #FFCC00;
        width: 10%;
        background: #FFCC00; }
    main section .my-information div {
      font-size: 1.2rem;
      padding: 0.5rem 0; }
      main section .my-information div:after {
        margin-top: 0.5rem;
        content: '';
        display: block;
        border: 0.1rem solid #CCC;
        background: #CCC; }
    main section .my-information span {
      font-weight: bold; }
